@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&display=swap);
html,body {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color:#dddddd;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App-main {
  width: 100%;
  background-image: url(/SingleStone-Frontend/static/media/photo-couch.ccd3c74c.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  min-height: 70vh;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Heading {
    background-color: #282c34;
    height: 4vh;
}
#logo {
    height: 12.5vh;
    width: 12.5vh;
    position: relative;
    left: 12vw;
    top: 1vh;
    /* Hexcode: #66e2bb */
    -webkit-filter: invert(78%) sepia(51%) saturate(397%) hue-rotate(104deg) brightness(96%) contrast(86%);
            filter: invert(78%) sepia(51%) saturate(397%) hue-rotate(104deg) brightness(96%) contrast(86%);
    
}
.Panel-container {
    text-align: center;
    margin-bottom: 50px;
}
.Panel-title {
    margin-top: 18px;
    font-size: 30px;
}

.Panel-cards-container {
    display: flex;
    justify-content: center;
}

@media only screen and (max-width: 990px) {
    .Panel-cards-container {
        flex-wrap: wrap;
    }
}
.Card-container {
    width: 250px;
    margin: 0px 15px;
}

.Card-content {
    margin: auto;
    margin-top: 10px;
    max-width: 235px;
}

.Card-number {
    font-size: 5vh;
    font-weight: 500;
    margin: 10px 0px;
}

.Card-title {
    margin: 0;
}

/* Pseudo element for underline of .Card-number */
h1:after {
    content: "";
    display: block;
    max-width: 50px;
    margin: auto;
    border-bottom: 3px solid #66e2bb;
    padding-top: 10px;
}


@media only screen and (max-width: 990px) {
    .Card-container {
        flex-basis: 33%;
    }
}

@media only screen and (max-width: 555px) {
    .Card-container {
        flex-basis: 100%;
    }
}
.Main-content {
    position: absolute;
    left: 12.5vw;
    top: 35vh;
    max-width: 650px;
}

#Content-1{
    font-size: 20px;
}
#Content-2{
    font-size: 40px;
    font-weight: 300;
    letter-spacing: 0.01em;
    margin: 20px auto;
}

#Content-3{
    font-size: 18px;
    max-width: 66%;
}

#Get-started {
    background-color: #66e2bb;
    border: none;
    font-weight: 600;
    font-size: 15px;
    letter-spacing: 0.08em;
    border-radius: 20px;
    padding: 8px 25px;
    margin-top: 20px;
    transition: 0.5s;
}

#Get-started:hover {
    cursor: pointer;
    background-color:#41a888;
}

a:link, a:visited {
    text-decoration: none;
    color: black;
}

@media only screen and (max-width: 555px) {
    .Main-content {
        top: 12.5vh;
    }

    #Content-3 {
        max-width: 98%;
    }
}

@media only screen and (max-width: 390px) {
    #Content-1 {
        font-size: 20px;
    }

    #Content-2 {
        font-size: 30px;
    }
}
